import React from "react";
import { Link } from "react-router-dom";
import HomePage from "./pages/HomePage";

function Navbar() {
  return (
    <nav>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/about">About</Link>
        </li>
        <li>
          <Link to="/article-list">Articles</Link>
        </li>
      </ul>
    </nav>
  );
}

export default Navbar;
